export default {
    props: {
        model: {
            type: Object,
            default: () => {},
        }
    },
    mounted() {
        $('#exampleModalCenter').modal('toggle');
        this.customJs();
    },
    methods: {
        customJs() {
            var parentElement = document.getElementById('exampleModalCenter');
            var pTags = parentElement.getElementsByTagName('p');
            for (var i = 0; i < pTags.length; i++) {
                var imgTag = pTags[i].getElementsByTagName('img')[0];
                if (imgTag) {
                    pTags[i].style.textAlign = "center";
                }
            }
        }
    },
}
